/* eslint-disable react/jsx-props-no-spreading */
import { memo } from 'react';
import { useForm } from 'react-hook-form';
import InputMask from 'react-input-mask';
// import { type UserInfo } from '../../type';
import { emailValidate, phoneValidate } from '../../utilize/validate';

export interface Step1Ref {
  onSubmit(): Promise<UserInfo>;
}

// type Step1Props = {
//   userInfo?: UserInfo,
//   onUpdateUserInfo: (userInfo: UserInfo) => void,
// };

const Step1 = memo(() => {
  // const { userInfo = {}, onUpdateUserInfo } = props;
  const {
    register,
    handleSubmit,
    formState: { errors },
  } = useForm();

  const atSubmit = (data) => {
    console.log(data);
  };

  return (
    <section className="step1-form-container" data-name="Step1">
      <form data-phase="address" onSubmit={handleSubmit(atSubmit)}>
        <h3 className="form-title"></h3>
        <section className="form-body ">
          {/* len 1 */}
          <div className="form-row">
            <div className="group input-title">
              <div className="input-label"></div>
              <div className="select-container">
                <select className="form-select" {...register('title')}>
                  <option value="mr"></option>
                  <option value="ms"></option>
                  <option value="mx"></option>
                </select>
              </div>
            </div>

            <div className="group input-name">
              <div className="input-label"></div>
              <input
                className="form-control"
                type="text"
                placeholder=""
                {...register('name', { required: true })}
              />
              {errors.name && <span> This field is required </span>}
            </div>
          </div>
          {/* len 2 */}
          <div className="form-row">
            <div className="group input-tel">
              <div className="input-label"></div>
              <InputMask
                className="form-control"
                type="tel"
                placeholder=""
                // mask="0\999-999999"
                {...register('tel', { required: true, pattern: phoneValidate })}
              />
              {errors.tel && <span> This field is required </span>}
            </div>
            <div className="group input-mail">
              <div className="input-label">Email</div>
              <input
                className="form-control"
                type="email"
                placeholder=""
                {...register('mail', {
                  required: true,
                  pattern: emailValidate,
                })}
              />
              {errors.mail && <span> This field is required </span>}
            </div>
          </div>
          {/* len 3 */}
          <div className="form-row">
            <div className="group input-city">
              <div className="input-label"></div>
              <div className="select-container">
                <select className="form-select" {...register('city')}>
                  <option value="" disabled>
                    
                  </option>
                  <option value="KLU"></option>
                  <option value="TPH"></option>
                  <option value="TPE"></option>
                  <option value="TYC"></option>
                  <option value="HSH"></option>
                  <option value="HSC"></option>
                  <option value="MAC"></option>
                  <option value="MAL"></option>
                  <option value="TXG"></option>
                  <option value="CWH"></option>
                  <option value="CWS"></option>
                  <option value="NTC"></option>
                  <option value="NTO"></option>
                  <option value="YLH"></option>
                  <option value="CHY"></option>
                  <option value="CYI"></option>
                  <option value="TNN"></option>
                  <option value="KHH"></option>
                  <option value="IUH"></option>
                  <option value="PTS"></option>
                  <option value="ILN"></option>
                  <option value="ILC"></option>
                  <option value="HWA"></option>
                  <option value="HWC"></option>
                  <option value="TTC"></option>
                  <option value="TTT"></option>
                  <option value="PEH"></option>
                  <option value="KMN"></option>
                  <option value="LNN"></option>
                </select>
              </div>
            </div>
            <div className="group input-address">
              <div className="input-label"></div>
              <input
                className="form-control"
                type="text"
                placeholder=""
                {...register('address', { required: true })}
              />
              {errors.address && <span> This field is required </span>}
            </div>
          </div>
        </section>
      </form>
    </section>
  );
});
export default Step1;
